import React from 'react';
import './style.css';

//  component:   //
export default function Footer() {
    //  event handler:       //
    const onInstaIconButtonClickHandler = () => {
        window.open('https://www.instagram.com');
    };
    //  event handler:        //
    const onNaverBlogIconButtonClickHandler = () => {
        window.open('https://blog.naver.com');
    }

    //  event handler:      //
    const onKeyDownHandler = (e: React.KeyboardEvent, callback: () => void) => {
        if (e.key === 'Enter' || e.key === ' ') {
            e.preventDefault();
            callback();
        }
    }

    //  render:    //
    return (
        <footer id='footer'>
            <div className='footer-container'>
                <div className='footer-top'>
                    <div className='footer-logo-box'>
                        <div className='icon-box'>
                            <div className='icon logo-light-icon' role='img' aria-label='DevHub '></div>
                        </div>
                        <div className='footer-logo-text'>{'DevHub'}</div>
                    </div>
                    <div className='footer-link-box'>
                        <div className='footer-email-link'>{'hsw5761@gmail.com'}</div>
                        <div className='icon-button' onClick={onInstaIconButtonClickHandler} onKeyDown={(e) => onKeyDownHandler(e, onInstaIconButtonClickHandler)} role='button' aria-label=' ' tabIndex={0}>
                            <div className='icon insta-icon' role='img' aria-label=' '></div>
                        </div>
                        <div className='icon-button' onClick={onNaverBlogIconButtonClickHandler} onKeyDown={(e) => onKeyDownHandler(e, onNaverBlogIconButtonClickHandler)} role='button' aria-label='  ' tabIndex={0}>
                            <div className='icon naver-blog-icon' role='img' aria-label='  '></div>
                        </div>
                    </div>
                </div>
                <div className='footer-bottom'>
                    <div className='footer-copyright'>{'Copyright @ 2024 Solpooh. All Rights Reserved.'}</div>
                </div>
            </div>
        </footer>
    )
}