import React, {ChangeEvent, KeyboardEvent, useEffect, useRef, useState} from 'react';
import './style.css';
import {useLocation, useNavigate, useParams} from 'react-router-dom';
import {
    AUTH_PATH,
    BOARD_DETAIL_PATH, BOARD_PATH,
    BOARD_UPDATE_PATH,
    BOARD_WRITE_PATH,
    MAIN_PATH,
    SEARCH_PATH,
    USER_PATH, YOUTUBE_PATH, YOUTUBE_TREND_PATH
} from '../../constants';
import {useCookies} from 'react-cookie';
import {useBoardStore, useEditorStore, useLoginUserStore} from '../../stores';
import {fileUploadRequest, patchBoardRequest, postBoardRequest} from '../../apis';
import {PatchBoardRequestDto, PostBoardRequestDto} from '../../apis/request/board';
import {PatchBoardResponseDto, PostBoardResponseDto} from '../../apis/response/board';
import {ResponseDto} from '../../apis/response';
import { convertToRaw, EditorState} from 'draft-js';
import SearchAutocomplete from '../../components/SearchAutocomplete';
import ThemeToggle from '../../components/ThemeToggle';

//  component:   //
export default function Header() {

    //  state:    //
    const { loginUser, setLoginUser, resetLoginUser } = useLoginUserStore();
    //  state: path  //
    const { pathname } = useLocation();
    //  state: cookie  //
    const [cookies, setCookie] = useCookies();
    //  state:   //
    const [isLogin, setLogin] = useState<boolean>(false);

    const isAuthPage = pathname.startsWith(AUTH_PATH());
    const isMainPage = pathname === MAIN_PATH();
    const isSearchPage = pathname.startsWith(SEARCH_PATH(''));
    const isBoardDetailPage = pathname.startsWith(BOARD_PATH() + '/' + BOARD_DETAIL_PATH('', ''));
    const isBoardWritePage = pathname.startsWith(BOARD_PATH() + '/' + BOARD_WRITE_PATH());
    const isBoardUpdatePage = pathname.startsWith(BOARD_PATH() + '/' + BOARD_UPDATE_PATH(''));
    const isUserPage = pathname.startsWith(USER_PATH(''));
    const isYoutubePage = pathname.startsWith(YOUTUBE_PATH());

    //  function:   //
    const navigate = useNavigate();

    //  event handler:      //
    const onLogoClickHandler = () => {
        if (pathname === MAIN_PATH()) {
            window.location.reload();
        } else {
            navigate(MAIN_PATH());
        }
    }
    const onYoutubeClickHandler = () => {
        if (pathname === YOUTUBE_PATH()) {
            window.location.reload();
        } else {
            navigate(YOUTUBE_PATH());
        }
    }
    const onTrendClickHandler = () => {
        if (pathname === YOUTUBE_TREND_PATH()) {
            window.location.reload();
        } else {
            navigate(YOUTUBE_TREND_PATH());
        }
    }

    //  event handler:      //
    const onKeyDownHandler = (e: React.KeyboardEvent, callback: () => void) => {
        if (e.key === 'Enter' || e.key === ' ') {
            e.preventDefault();
            callback();
        }
    }

    //  component:    //
    const SearchButton = () => {
        //  state:   //
        const [word, setWord] = useState<string>('');
        //  state:  path variable  //
        const { searchWord } = useParams();

        //  event handler:      //
        const onSearchWordChange = (value: string) => {
            setWord(value);
        };

        //  event handler:     //
        const onSearch = (value: string) => {
            if (!value.trim()) {
                alert(' .');
                return;
            }
            navigate(SEARCH_PATH(value));
        };

        //  effect:  path variable     //
        useEffect(() => {
            if (searchWord) {
                setWord(searchWord);
            }
        }, [searchWord]);

        //  effect: login user     //
        useEffect(() => {
            setLogin(loginUser !== null);
        }, [loginUser])

        // render:     //
        return (
            <div className='header-search-box'>
                <SearchAutocomplete
                    value={word}
                    onChange={onSearchWordChange}
                    onSearch={onSearch}
                    placeholder=' .'
                />
            </div>
        );
    }

    //  component:    //
    const MyPageButton = () => {
        //  state: userEmail path variable  //
        const { userEmail } = useParams();

        //  event handler:       //
        const onMyPageButtonClickHandler = () => {
            if (!loginUser) return;
            const { email } = loginUser;
            navigate(USER_PATH(email));
        }
        //  event handler:       //
        const onSignOutButtonClickHandler = () => {
            // eslint-disable-next-line no-restricted-globals
            if (!confirm("  ?")) return;
            resetLoginUser();
            setCookie('accessToken', '', { path: '/', expires: new Date() });
            setLogin(false);
            navigate(MAIN_PATH());
        }

        //  event handler:       //
        const onSignInButtonClickHandler = () => {
            navigate(AUTH_PATH());
        }

        //  effect:   isLogin true   //
        useEffect(() => {
            //       
            if (!cookies.accessToken) {
                setLogin(false);
            } else {
                setLogin(true);
            }
        }, []);

        //    render:      //
        if(isLogin && (userEmail === loginUser?.email))
        return(
            <div className='black-button' onClick={onSignOutButtonClickHandler} onKeyDown={(e) => onKeyDownHandler(e, onSignOutButtonClickHandler)} role='button' aria-label='' tabIndex={0}>{''}</div>
        )
        if(isLogin)
        //    render:      //
        return(
            <div className='white-button' onClick={onMyPageButtonClickHandler} onKeyDown={(e) => onKeyDownHandler(e, onMyPageButtonClickHandler)} role='button' aria-label=' ' tabIndex={0}>{''}</div>
        )
        //    render:      //
        return(
            <div className='black-button' onClick={onSignInButtonClickHandler} onKeyDown={(e) => onKeyDownHandler(e, onSignInButtonClickHandler)} role='button' aria-label='  ' tabIndex={0}>{''}</div>
        )
    };

    //  component:    //
    const UploadButton = () => {
        //  state:   path variable  //
        const { boardNumber } = useParams();
        //  state:   //
        const { title, content, category, boardImageFileList, resetBoard } = useBoardStore();
        //  state: EditorState  //
        const { editorState, setEditorState } = useEditorStore();

        //  function:   //
        const navigator = useNavigate();

        //  function: post board response   //
        const postBoardResponse = (responseBody: PostBoardResponseDto | ResponseDto | null) => {
            if (!responseBody) return;
            const { code } = responseBody;
            if (code === 'DBE') alert(' .');
            if (code === 'AF' || code === 'NU') navigate(AUTH_PATH());
            if (code === 'VF') alert('  .');
            if (code !== 'SU') return;

            resetBoard();
            if (!loginUser) return;
            const { email } = loginUser;
            navigate(USER_PATH(email));
        }
        //  function: patch board response   //
        const patchBoardResponse = (responseBody: PatchBoardResponseDto | ResponseDto | null) => {
            if (!responseBody) return;
            const { code } = responseBody;
            if (code === 'AF' || code === 'NU' || code == 'NB' || code == 'NP') navigate(AUTH_PATH());
            if (code === 'VF') alert('  .');
            if (code === 'DBE') alert(' .');
            if (code !== 'SU') return;

            if (!boardNumber) return;
            navigate(BOARD_PATH() + '/' + BOARD_DETAIL_PATH(category, boardNumber));
        }

        //  event handler:       //
        const onUploadButtonClickHandler = async () => {
            // eslint-disable-next-line no-restricted-globals
            if (!confirm("  ?")) return;

            const accessToken = cookies.accessToken;
            if (!accessToken) {
                alert("  .");
                navigator(AUTH_PATH());
                return;
            }

            const boardImageList: string[] = [];

            for (const file of boardImageFileList) {
                const data = new FormData();
                data.append('file', file.file);

                const url = await fileUploadRequest(data);
                if (url) boardImageList.push(url);
            }

            //  editorState  URL  S3 URL 
            let newEditorState = editorState;
            const contentState = newEditorState.getCurrentContent();
            const blockMap = contentState.getBlockMap();

            let imageIndex = 0;

            blockMap.forEach((block: any) => {
                const entityKey = block.getEntityAt(0);

                if (entityKey) {
                    const entity = contentState.getEntity(entityKey);
                    const entityData = entity.getData();

                    if (entity.getType() === 'IMAGE' && boardImageList[imageIndex]) {
                        const newImageUrl = boardImageList[imageIndex]; // index 
                        imageIndex++; //  URL 

                        const newEntityData = {...entityData, src: newImageUrl};
                        const contentStateWithUpdatedEntity = contentState.replaceEntityData(entityKey, newEntityData);

                        newEditorState = EditorState.push(
                            newEditorState,
                            contentStateWithUpdatedEntity,
                            'apply-entity'
                        );
                    }
                }
            });

            // JSON 
            const updatedContent = JSON.stringify(
                convertToRaw(newEditorState.getCurrentContent())
            );

            //       
            const isWritePage = pathname === BOARD_PATH() + '/' + BOARD_WRITE_PATH();
            if (isWritePage) {
                const requestBody: PostBoardRequestDto = {
                    title,
                    content: updatedContent, //  content 
                    category,
                    boardImageList,
                };
                postBoardRequest(requestBody, accessToken).then(postBoardResponse);
            } else {
                if (!boardNumber) return;
                const requestBody: PatchBoardRequestDto = {
                    title,
                    content: updatedContent,
                    category,
                    boardImageList,
                };
                patchBoardRequest(boardNumber, requestBody, accessToken).then(patchBoardResponse);
            }
        };

        //  render:     //
        if (title && content && category)
        return <div className='black-button' onClick={onUploadButtonClickHandler} onKeyDown={(e) => onKeyDownHandler(e, onUploadButtonClickHandler)} role='button' aria-label=' ' tabIndex={0}>{''}</div>;
        //  render:      //
        return <div className='disable-button' role='button' aria-label='  (, ,  )' aria-disabled='true'>{''}</div>;
    }
    //  render:    //
    return (
        <header id='header'>
            <div className='header-container'>
                <nav className='header-left-box' aria-label=' '>
                    <div className='header-leftOne-box' onClick={onLogoClickHandler} onKeyDown={(e) => onKeyDownHandler(e, onLogoClickHandler)} role='button' aria-label=' ' tabIndex={0}>
                        <div className='icon-box'>
                            <div className='icon logo-dark-icon' role='img' aria-label='DevHub '></div>
                        </div>
                        <div className='header-logo'>{'DevHub'}</div>
                    </div>
                    <div className='header-leftTwo-box' onClick={onYoutubeClickHandler} onKeyDown={(e) => onKeyDownHandler(e, onYoutubeClickHandler)} role='button' aria-label='DevTube ' tabIndex={0}>
                        <div className='icon-box'>
                            <div className='icon logo-youtube-icon' role='img' aria-label='YouTube '></div>
                        </div>
                        <div className='header-logo'>{'DevTube'}</div>
                    </div>
                    <div className='header-leftThree-box' onClick={onTrendClickHandler} onKeyDown={(e) => onKeyDownHandler(e, onTrendClickHandler)} role='button' aria-label='  ' tabIndex={0}>
                        <div className='icon-box'>
                            <div className='icon logo-trend-icon' role='img' aria-label=' '></div>
                        </div>
                        <div className='header-logo'>{'Trend'}</div>
                    </div>
                </nav>
                <div className='header-right-box'>
                    {/*{(isMainPage || isSearchPage || isBoardDetailPage) && <SearchButton />}*/}
                    {/*{(isMainPage || isSearchPage || isBoardDetailPage || isUserPage || isYoutubePage) && <MyPageButton />}*/}
                    {!isYoutubePage && <SearchButton />}
                    <ThemeToggle />
                    <MyPageButton />
                    {(isBoardWritePage || isBoardUpdatePage) && <UploadButton />}
                </div>
            </div>
        </header>
    )
}